.nav-dashbroard {
  margin-left: 300px;
  margin-top: 10px;
  display: flex;
  position: absolute;
}
.nav-link-sub {
  display: flex;
  color: #5e6278;
  background-color: #ffffff;
  font-weight: 600 !important;
  border-radius: 0.475rem;
  width: 120px;
  height: 35px;
  border: 1px solid #0000;
  font-size: 13px;
  margin-top: 10px;
  margin-left: 20px;
}

.sub-title {
  text-align: center;
  display: flex;
  margin: auto;
}
.activated-sub {
  background-color: #009ef7;
  color: #ffffff;
}
@media (min-width: 1921px) {
  .nav-link-sub {
    margin-top: 50px;
    margin-left: 70px;
  }
}
@media (min-width: 1026px) and (max-width: 1440px) {
  .nav-link-sub {
    margin-top: 20px;
    margin-left: 20px;
  }
}
@media (min-width: 770px) and (max-width: 1025px) {
  .nav-link-sub {
    margin-top: 20px;
    margin-left: 10px;
  }
}
@media (min-width: 740px) and (max-width: 769px) {
  .nav-link-sub {
    margin-top: 20px;
    margin-left: 10px;
  }
}
@media (max-width: 430px) {
  .nav-link-sub {
    margin-top: 20px;
    margin-left: 10px;
  }
  .nav-dashbroard {
    margin-left: 100px;
  }
}
@media (max-width: 376px) {
  .nav-link-sub {
    margin-top: 20px;
    margin-left: 10px;
  }
  .nav-dashbroard {
    margin-left: 60px;
  }
}
