.wrapper-header {
  width: 100%;
  margin-top: -8px;
  height: 70px;
  background-color: #f5f8fa;
  display: flex;
  justify-content: center;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12);
}
.content-header {
  height: 100%;
  width: 1150px;
  display: flex;
  position: relative;
  justify-content: space-between;
}
.title-header {
  margin-left: -1800px;
  margin-top: 12px;
  font-size: 30px;
}
.icon-header {
  padding: 20px;
  margin-right: -400px;
}
.icon {
  height: 30px;
  width: 30px;
  margin-right: 30px;
  color: #babcc5;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.475rem;
  padding: 0;
  cursor: pointer;
}
.icon:hover {
  background-color: #dddddd;
  color: #4e4f55;
}
.icon-user {
  padding-right: 30px;
  cursor: pointer;
}
.log-out {
  width: 250px;
  height: 350px;
  border-radius: 12px;
  box-shadow: 0px 4px 32px 0px rgba(0, 0, 0, 0.1);
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  margin-top: 7px;
  margin-left: 80px;
  position: absolute;
  z-index: 2;
  padding-top: 8px;
}
.log-out > ul > li > svg {
  margin-left: 5px;
  position: absolute;
  margin-top: 5px;
}
.log-out > ul > li > span {
  margin-left: 70px;
}

.log-out > ul {
  list-style: none;
  cursor: pointer;

  margin-top: 10px;
  margin-left: 5px;
  padding-left: 20px;
  width: 222px;
}
.log-out > ul > li {
  text-align: left;
  padding-top: 7px;
  padding-bottom: 10px;
  font-size: 24px;
  border-radius: 7px;
  padding-left: 4px;
  transition: background-color ease-in-out 200ms;
}
.log-out > ul > li:hover {
  background-color: #bebebe;
}
.btn-secondary {
  display: none;
}

@media (min-width: 1921px) {
  .title-header {
    margin-left: -2760px;
  }
  .icon-header {
    margin-right: -720px;
  }
}
@media (min-width: 1026px) and (max-width: 1440px) {
  .title-header {
    margin-left: -1090px;
    margin-top: 14px;
  }
  .icon-header {
    margin-right: -160px;
  }
}
@media (min-width: 770px) and (max-width: 1025px) {
  .title-header {
    margin-top: 20px;
    margin-left: -540px;
    font-size: 25px;
  }
  .icon-header {
    margin-right: -20px;
  }
}
@media (min-width: 740px) and (max-width: 769px) {
  .title-header {
    margin-top: 20px;
    margin-left: -300px;
    font-size: 25px;
  }
  .icon-header {
    margin-right: -20px;
  }
}
@media (max-width: 430px) {
  .title-header {
    margin-left: 10px;
    margin-top: 20px;
    font-size: 25px;
  }
  .icon {
    display: none;
  }
  .icon-header {
    margin-right: -30px;
  }
  .btn-secondary {
    margin-left: 25px;
    margin-top: 20px;
    background-color: transparent;
    color: #4e4f55;
    border: none;
    display: block;
  }
  .btn-secondary:focus :active {
    background-color: transparent;
  }

  .nav-link-mobile {
    margin-left: 10px;
    color: black;
  }
  .log-out {
    margin-left: -220px;
    margin-top: 15px;
  }
}
@media (max-width: 376px) {
  .title-header {
    margin-left: 10px;
    margin-top: 20px;
    font-size: 25px;
  }
  .icon {
    display: none;
  }
  .icon-header {
    margin-right: -30px;
  }
  .btn-secondary {
    margin-left: 25px;
    margin-top: 20px;
    background-color: transparent;
    color: #4e4f55;
    border: none;
    display: block;
  }
  .btn-secondary:focus :active {
    background-color: transparent;
  }

  .nav-link-mobile {
    margin-left: 10px;
    color: black;
  }
  .log-out {
    margin-left: -230px;
    margin-top: 15px;
  }
}
