.wrapper-nav {
  display: flex;
  flex-direction: column;
  float: left;
  width: 265px;
  min-height: 100vh;
  background-color: #1e1e2d;
}
.nav {
  margin-top: 20px;
}
.nav-link {
  color: #b5b5c3;
  text-align: justify;
  display: flex;
  font-size: 13px;
  margin-left: 16px;
  padding: 0.5rem 1rem;
  transition: none;
  width: 230px;
  height: 35px;
  border-radius: 0.475rem;
}
.nav-link:hover {
  color: #ffffff;
}

.activated {
  color: #ffffff;
  transition: color 0.2s ease;
  background-color: #2a2a3c;
  border-radius: 0.475rem;
}

.bullet {
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: #b5b5c3;
  margin-right: 16px;
  margin-top: 8px;
}
@media (min-width: 770px) and (max-width: 1025px) {
  .wrapper-nav {
    width: 195px;
  }
  .nav-link {
    width: 160px;
    height: 35px;
  }
}
@media (min-width: 740px) and (max-width: 769px) {
  .wrapper-nav {
    width: 165px;
  }
  .nav-link {
    width: 130px;
    height: 55px;
  }
}
@media (max-width: 430px) {
  .wrapper-nav {
    display: none;
  }
}
