.wrapper-setting {
  width: 100%;
  height: 100vh;

  background-color: #f2f2f2;
}
.form-setting {
  width: 530px;
  height: 650px;
  padding: 30px;
  position: absolute;
  margin-top: 150px;
  margin-left: 780px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}
.title-setting {
  margin-top: 100px;
  margin-left: 1px;
}
.title-setting > label {
  margin-right: 15px;
}
.title-setting > input {
  height: 40px;
  font-size: 18px;
  width: 340px;
  border-radius: 5px;
  border: 2px solid #a6a5a5;
  padding-left: 15px;
}
.title-error {
  font-size: 15px;
  margin-left: 95px;
  color: brown;
  position: absolute;
}
.email-setting {
  margin-top: 50px;
  margin-left: 5px;
}
.email-setting > label {
  margin-right: 15px;
}
.email-setting > input {
  height: 40px;
  font-size: 18px;
  width: 330px;
  border-radius: 5px;
  border: 2px solid #a6a5a5;
  padding-left: 15px;
}
.email-error {
  font-size: 15px;
  margin-left: 190px;
  color: brown;
  position: absolute;
}
.bg-setting {
  margin-top: 40px;
  margin-left: 1px;
}
.bg-setting > label {
  margin-right: 50px;
}
.bg-input-color {
  border-top: 2px solid #a6a5a5;
  border-left: 2px solid #a6a5a5;
  border-bottom: 2px solid #a6a5a5;
  width: 50px;
  height: 40px;
  border-radius: 5px 0 0 5px;
  margin-left: 10px;
  background-color: #ffff;
  padding-left: 5px;
  position: absolute;
}
.bg-input-color-text {
  height: 40px;
  width: 128px;
  border: 2px solid #a6a5a5;
  padding-left: 33px;
  font-size: 17px;
  border-radius: 5px;
  margin-left: 50px;
}
.bgColor-error-error {
  font-size: 15px;
  margin-left: 282px;
  color: brown;
  position: absolute;
}
.date-setting {
  margin-top: 50px;
}
.start-date-error {
  font-size: 13px;
  margin-left: 155px;
  color: brown;
  position: absolute;
}
.end-date-error {
  font-size: 13px;
  margin-left: 310px;
  color: brown;
  position: absolute;
}
.save-setting {
  margin-top: 80px;
}
.save-setting > button {
  width: 100px;
  height: 35px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  background-color: #009ef7;
  color: #ffffff;
}
.save-setting:hover > button {
  background-color: #0c8ed9;
}
@media screen and (min-width: 1921px) {
  .form-setting {
    margin-top: 150px;
    margin-left: 1080px;
  }
}
@media (min-width: 1026px) and (max-width: 1440px) {
  .form-setting {
    margin-top: 150px;
    margin-left: 550px;
  }
}
@media (min-width: 770px) and (max-width: 1025px) {
  .form-setting {
    margin-top: 150px;
    margin-left: 350px;
  }
}
@media (min-width: 740px) and (max-width: 769px) {
  .form-setting {
    margin-top: 150px;
    margin-left: 200px;
  }
}
@media (max-width: 430px) {
  .form-setting {
    margin-top: 150px;
    margin-left: 25px;
    width: 400px;
    height: 650px;
  }
  .title-setting > input {
    height: 40px;
    width: 250px;
  }
  .email-setting > input {
    width: 240px;
  }
  .bg-input-color-text {
    width: 88px;
  }
  .bg-setting > label {
    margin-right: 0px;
    margin-left: 0px;
  }
  .date-setting {
    margin-top: 30px;
  }
  .save-setting {
    margin-top: 60px;
  }
  .title-error {
    margin-left: 10px;
  }
  .email-error {
    margin-left: 130px;
  }
  .bgColor-error-error {
    margin-left: 195px;
  }
  .start-date-error {
    margin-left: 30px;
  }
  .end-date-error {
    margin-left: 190px;
  }
}
@media (max-width: 376px) {
  .form-setting {
    margin-top: 150px;
    margin-left: 25px;
    width: 350px;
    height: 650px;
  }
  .title-setting > input {
    height: 40px;
    width: 220px;
  }
  .email-setting > input {
    width: 210px;
  }
  .bg-input-color-text {
    width: 58px;
    padding-left: 10px;
  }
  .bg-setting > label {
    margin-right: 0px;
    margin-left: 0px;
  }
  .date-setting {
    margin-top: 30px;
  }
  .save-setting {
    margin-top: 60px;
  }
  .title-error {
    margin-left: 10px;
  }
  .email-error {
    margin-left: 110px;
  }
  .bgColor-error-error {
    margin-left: 165px;
  }
  .start-date-error {
    margin-left: 10px;
  }
  .end-date-error {
    margin-left: 165px;
  }
}
